<template>
  <h1>Game</h1>
  <template v-if="websocketConnected">
    <ShowGame :gameId="gameId" />
  </template>
  <p v-else>
    Sorry, the server is offline at the moment. Please try again later.
  </p>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import ShowGame from "@/components/ShowGame.vue";
import store from "@/store";

export default defineComponent({
  name: "SingleGame",
  components: {
    ShowGame
  },
  props: {
    gameId: {
      type: Number,
      required: true
    }
  },
  computed: {
    websocketConnected() {
      return store.getters.websocketConnected;
    }
  },
  mounted() {
    document.title = "Game Server | Game";
  }
});
</script>
